@import "definitions";

.logo {
  white-space: pre;
  @include rainbow;

  display: flex;
  justify-content: center;

  font-size: $font-size-base;
  height: 336px;
  @include breakpoint-down($breakpoint-xl) {
    font-size: 12px;
    height: 247px; }
  @include breakpoint-down($breakpoint-lg) {
    font-size: 10px;
    height: 208px; }
  @include breakpoint-down($breakpoint-md) {
    font-size: 8px;
    height: 167px; }
  @include breakpoint-down($breakpoint-sm) {
    font-size: 6px;
    height: 125px; }
  @include breakpoint-down($breakpoint-xs) {
    font-size: 5px;
    height: 104px; } }

.logo--kling {
  align-self: center; }

.logo--klang {
  align-self: flex-end; }

.logo--flut {
  align-self: flex-start; }
